:root{
    font-size: 12px;
    --color-main : rgb(224, 255, 232);
    --color-1 : rgb(77, 86, 79);
}
.Playlist{
    min-height: max-content;
}

.img-playlist{
    position: relative;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    min-height: max-content;
    max-width: 100vw;
    margin-top: 20px;
    gap: 10px;
    background-color: black;
}

.playlist-title{
    text-align: center;
    margin-top: 50px
}
.playlist-image{
    max-width: 300px;
    max-height: 300px;
    border-radius: 5px;
}
.image-container{
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: max-content;
}
.image-container h3{
    color: var(--color-main);
    text-align: center;
    font-size: clamp(.5rem, 4vw, 1rem);
}
.play-video-playlist {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(10px);
    transition: all .5s ease;
    opacity:1;
}

.play-video-playlist-hide{
    backdrop-filter: blur(0);
    position: fixed;
    z-index: -100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .5s ease;
    opacity:0;
}